import * as yup from 'yup';

const validateNames = (arr, value) => {
  const result = arr.find((el) => el?.propertyName.toLowerCase() === value?.toLowerCase());
  return !result;
};

export const addPropertyValidationSchema = (properties) => {
  return yup.object().shape({
    propertyName: yup
      .string()
      .typeError('  ')
      .test('sameName', '     ', (value) => {
        if (!value) return true; //   ,     required
        return validateNames(properties, value); //  false,       
      })
      .trim(' ')
      .required(' '),
    propertyType: yup.string().typeError('  ').required(' '),
  });
};