import * as yup from 'yup';

export const loginValidationSchema = yup.object().shape({
  email: yup
    .string()
    .typeError('  ')
    .min(4, '     4 ')
    .max(20, '     20 ')
    .required(' '),
  password: yup
    .string()
    .typeError('  ')
    .min(8, '     8 ')
    .required(' '),
});