import * as yup from 'yup';

export const registrationValidationSchema = yup.object().shape({
  name: yup
    .string()
    .min(4, '     4 ')
    .max(20, '     20 ')
    .typeError('  ')
    .required(' '),
  secondName: yup.string().typeError('  ').required(' '),
  password: yup
    .string()
    .min(8, '     8 ')
    .typeError('  ')
    .required(' '),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref('password')], '  ')
    .required(' '),
  email: yup.string().email('  email').required(' '),
});