import * as yup from 'yup';

export const addItemValidationSchema = yup.object().shape({
  itemId: yup.boolean(),
  itemName: yup.string().typeError('  ').trim(' ').required(' '),
  price: yup
    .number()
    .typeError('  ')
    .integer('   ')
    .test('firstSymbol', '    ', (value) => {
      return value?.toString().charAt(0) !== '0'; //     0
    })
    .required(' '),
  file: yup.object().when('itemId', {
    is: true,
    then: yup
      .object()
      .shape({
        file: yup.mixed().test('fileSize', '     150', (value) => {
          return value ? value.size < 153600 : true;
        }),
        type: yup
          .string()
          .oneOf(['image/jpeg', 'image/png', 'image/pjpeg'], '     .jpg,.jpeg,.png'),
        name: yup.string(),
      })
      .nullable()
      .typeError(' '),
    otherwise: yup
      .object()
      .shape({
        file: yup
          .mixed()
          .test('fileSize', '     150', (value) => {
            return value ? value.size < 153600 : true;
          })
          .typeError(' ')
          .required(' '),
        type: yup
          .string()
          .oneOf(['image/jpeg', 'image/png', 'image/pjpeg'], '     .jpg,.jpeg,.png')
          .typeError(' ')
          .required(' '),
        name: yup.string().typeError(' ').required(' '),
      })
      .required(),
  }),
  fileUrl: yup.string().nullable().typeError('  '),
  description: yup.string().typeError('  ').required(' '),
  propertiesOfProduct: yup.array().of(
    yup
      .object()
      .shape({
        id: yup.string().typeError('  ').required(' '),
        propertyName: yup.string().typeError('  ').required(' '),
        propertyType: yup.string().typeError('  ').required(' '),
        propertyValue: yup.lazy((value) => {
          switch (typeof value) {
            case 'number':
              return yup.number().typeError('  ').required(' ');
            case 'string':
              return yup.string().typeError('  ').required(' ');
            default:
              return yup
                .array()
                .of(
                  yup.object().shape({
                    propertyValue: yup.string().typeError('  ').required(' '),
                  }),
                )
                .required(' ');
          }
        }),
      })
      .required(' '),
  ),
});