import * as yup from "yup";

export const productCardValidationSchema = yup.object().shape({
    itemName: yup.string().typeError('  ').required(' '),
    description: yup.string().typeError('  ').required(' '),
    fileUrl: yup.string().typeError('  ').required(' '),
    price: yup
        .number()
        .typeError('  ')
        .integer('   ')
        .required(' '),
    propertiesOfProduct: yup.array().of(
        yup.object().shape({
            id: yup.string(),
            propertyName: yup.string(),
            propertyType: yup.string(),
            propertyValue: yup.string(),
        }),
    ),
});